import React from 'react';
import { NavLink } from 'react-router-dom';
import "./NavBar.css";
import Nav from 'react-bootstrap/Nav';

const base = process.env.PUBLIC_URL;
const NavBar = props => 
  (
  <Nav className="mr-auto">
      <NavLink exact to={base + "/"} className="navlink" activeClassName="active-link">
        Hub
      </NavLink>
      <NavLink exact to={base + "/zones"} className="navlink" activeClassName="active-link">
        Zoner
      </NavLink>
      <NavLink to={base + "/settings"} className="navlink" activeClassName="active-link">
        Inställningar
      </NavLink>
      <NavLink to={base + "/rpio"} className="navlink" activeClassName="active-link">
        Rpio
      </NavLink>
      <NavLink to={base + "/login"} className="navlink" activeClassName="active-link">
        Login
      </NavLink>
      <NavLink to={base + "/register"} className="navlink" activeClassName="active-link">
        Register
      </NavLink>
    {props.children}
  </Nav>
);

export default NavBar;
